function MainTable(props:any) {
  const [value, setValue] = React.useState(0);
  const [open, setOpen] = React.useState(false);
  const [openEditModal, setOpenEditModal] = React.useState(false);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOk = () => {
    setOpen(false);
  };

  const handleEditClickOpen = () => {
    setOpenEditModal(true);
  };

  const handleEditClose = () => {
    setOpenEditModal(false);
  };

  const handleEditOk = () => {
    setOpenEditModal(false);
  };


  return (
    <PaperHeadingContainer heading={"List Leads"}>
      <TableContainer>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="">
          <Tab label="Fresh"  />
          <Tab label="Active"  />
          <Tab label="Archived"  />
        </Tabs>
      </Box>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
            <TableCell padding="checkbox"
                    onClick={() => {}}
                  >
                    <Checkbox
                      color="primary"
                      checked={false}
                    />
                </TableCell>
                {/*
                props.data.headCells ? props.data.headCells.map((heading:string, key:number)=>{
                  return <TableCell key={key}>{heading}</TableCell>
                }) : null
                */}

                {
                  props.data.headCells ? props.data.headCells.map((data:any, key:number)=>{
                    return <TableCell align={key==0?"left":"right"} key={key}>{data.label}</TableCell>
                  }) : null
                }
              
              <TableCell padding="checkbox">
                <div style={{width: "100px", textAlign: "center"}}>
                  Actions
                </div>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {
                  props.data.rows ? props.data.rows.map((data:any, key:number)=>{
                    
              return <TableRow key={key}>
                  <TableCell padding="checkbox"
                    onClick={() => {}}
                  >
                    <Checkbox
                      color="primary"
                      checked={false}
                    />
                </TableCell>

                {
                  Object.keys(data).map((a:any, k:number)=>{
                      return <TableCell key={k} align={k==0?"left":"right"} >{data[a]}</TableCell>
                  })
                }


                <TableCell align="right">
                      <div style={{display: "flex", width: "100px", justifyContent: "center"}} onClick={(e:any)=>{e.preventDefault()}}>
                        <a href={`/`} title="Edit">
                          <EditRoundedIcon color="action" fontSize="small" 
                            onClick={()=>{setOpenEditModal(true)}}
                          />
                        </a>
                          <EnhancedModal 
                            heading={"Update Lead"}
                            content={"asd"}
                            open={openEditModal}
                            handleClickOpen={handleEditClickOpen}
                            handleClose={handleEditClose}
                            handleOk={handleEditOk}
                          />
                          <Switch
                            size="small"
                            checked={true}
                            onChange={()=>{

                            }}
                            inputProps={{ 'aria-label': 'controlled' }}
                          />
                          <CloseIcon color="action" style={{cursor : "pointer"}} fontSize="small" onClick={(e:any)=>{e.preventDefault();handleClickOpen()}}/>
                          
                          <EnhancedModal 
                            heading={"Are you sure, you want to delete this record?"}
                            content={""}
                            open={open}
                            handleClickOpen={handleClickOpen}
                            handleClose={handleClose}
                            handleOk={handleOk}
                          />
                      </div>
                        
                      </TableCell>
              </TableRow>
            }) : null
          }
          </TableBody>
        </Table>
        <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={2}
            rowsPerPage={2}
            page={0}
            onPageChange={()=>{}}
            onRowsPerPageChange={()=>{}}
          />
      </TableContainer>
    </PaperHeadingContainer>
  )
}

